//시간표 등록

.tableBox{
  width: 1000px;
  height: 100%;
  border: 1px solid red;
  padding: 5px;
  margin: 0 auto;
  text-align: center;

  input{
    margin: 0 10px;
    padding: 7px 10px;
  }
  select{
    color: #333;
    padding: 7px 10px;
    margin: 0 10px;
  }
  .color{
    padding: 0px;
    height: 35px;
    /*위치조정*/
  }
  button{
    border: none;
    background-color: transparent;
    font-size: 14px;
    background-color: aqua;   /*색상 변경예정*/
    padding: 8px 16px;
    border-radius: 5px;
    cursor: pointer;
    &:hover{
      font-weight: bold;
    }
  }
}